.App-logo {
    max-width: 10%;
    animation: App-logo-spin infinite 20s linear;
    pointer-events: none;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .head {
      display: flex;
  }

  .head h1 {
      width: 90%;
      font-size: 3em;
      text-align: center;
      font-family: 'Akaya Telivigala', cursive;
  }


  @media(max-width:1024px) {
    .head h1 {
      font-size: 1.5em;
    }

    .App-logo {
      width: 5%;
    }
  }
  
  