body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #3F4A5A;
  color: rgb(228, 228, 228);
}

header {
  width: 100%;
  height: 25vh;
  padding: 3vh 2vh;
}

main {
  display: flex;
  width: 100%;
  max-height: 100vh;
}

.menu {
  width: 40%;
}

.page {
  text-align: justify;
  width: 80%;
}

@media (max-width: 1024px) {
  header {
    height: 18vh;
    padding: 0 0 10px 10px;
  }
}
.App-logo {
    max-width: 10%;
    animation: App-logo-spin infinite 20s linear;
    pointer-events: none;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .head {
      display: flex;
  }

  .head h1 {
      width: 90%;
      font-size: 3em;
      text-align: center;
      font-family: 'Akaya Telivigala', cursive;
  }


  @media(max-width:1024px) {
    .head h1 {
      font-size: 1.5em;
    }

    .App-logo {
      width: 5%;
    }
  }
  
  
.menu > ul {
    list-style-type: none;
}

.menu > ul > li > a {
    display: block;
    color: rgb(228, 228, 228);
    letter-spacing: 2.5px;
    text-transform: uppercase;
    text-decoration: none;
    border-left: 10px solid;
    margin-left: 6%;
    font-size: 1.5em;
    transition: .4s;
}

.menu > ul > li {
    margin-bottom: 10%;
}

.menu ul li a:hover {
    transform: translateX(20px);
}

.menu ul li a.active {
    transform: translateX(0)
}


.menu > ul > li:nth-child(1) > a.active {
    border-color: rgba(115, 226, 245, 1);
}

.menu > ul > li:nth-child(2) > a.active {
    border-color: rgba(73, 184, 66, 1);
}

.menu > ul > li:nth-child(3) > a.active {
    border-color: rgba(128, 24, 197, 1);
}

.menu > ul > li:nth-child(4) > a.active {
    border-color: rgba(235, 39, 26, 1);
}

.menu > ul > li:nth-child(5) > a.active {
    border-color: rgba(219, 203, 12, 1);
}

.menu > ul > li:nth-child(6) > a.active {
    border-color: rgb(12, 219, 140);
}


@media(max-width: 1024px) {
    .menu ul li {
        font-size: 6px;
        line-height: 25px;
    }

    .menu {
        width: 130px;
    }
}

.description {
    padding-right: 30px;
}

.description h1 {
    padding-bottom: 20px;
}

.description article {
    text-indent: 15px;
}

@media(max-width: 1024px) {
    .description h1 {
        font-size: 15px;
    }

    .description {
        width: 100%;
        padding: 0 20px 0 15px;
        
    }

    .description article {
        font-size: 10px;
        text-indent: 7px;
    }
}
.description article p {
    padding: 10px 0;
    font-weight: bold;
    color: wheat;
    text-decoration: underline;
}


.description article ul {
    list-style-type: none;
}

.description article ul a {
    text-decoration: none;
    color: white;
}
.description article ul a:hover {
    cursor: pointer;
    color:crimson;
}




.foot {
    position: fixed;
    bottom: 0;
    background-color: black;
    width: 100%;
    text-align: center;
    padding: 2px 0;
    font-size: 12px;
}


