.description article p {
    padding: 10px 0;
    font-weight: bold;
    color: wheat;
    text-decoration: underline;
}


.description article ul {
    list-style-type: none;
}

.description article ul a {
    text-decoration: none;
    color: white;
}
.description article ul a:hover {
    cursor: pointer;
    color:crimson;
}



