* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #3F4A5A;
  color: rgb(228, 228, 228);
}

header {
  width: 100%;
  height: 25vh;
  padding: 3vh 2vh;
}

main {
  display: flex;
  width: 100%;
  max-height: 100vh;
}

.menu {
  width: 40%;
}

.page {
  text-align: justify;
  width: 80%;
}

@media (max-width: 1024px) {
  header {
    height: 18vh;
    padding: 0 0 10px 10px;
  }
}